// Pathify
import { make } from 'vuex-pathify'

// Data
const userProfile = window.localStorage.profile

const state = {
  drawer: null,
  drawerImage: true,
  mini: true,
  items: [],
}

const loginPage = {
  title: 'Login Screen',
  icon: 'mdi-user',
  to: '/',
}
const homePage = {
  title: 'Home',
  icon: 'mdi-home',
  to: '/home',
}
const dashboardPage = {
  title: 'Dashboard',
  icon: 'mdi-view-dashboard',
  to: '/components/dashboard',
}
const financialPage = {
  title: 'Financeiro',
  icon: 'mdi-cash',
  to: '/components/financial/',
}
const userProfilePage = {
  title: 'Perfil de Usuário',
  icon: 'mdi-account',
  to: '/components/profile/',
}

const itemsMaster = [
  homePage,
  dashboardPage,
  financialPage,
  userProfilePage,
]
const itemsAdmin = [
  homePage,
  financialPage,
  userProfilePage,
]
const itemsFinancial = [
  homePage,
  financialPage,
  userProfilePage,
]

if (userProfile === 'MASTER') {
  state.items = itemsMaster
} else if (userProfile === 'FINANCIAL') {
  state.items = itemsFinancial
} else if (userProfile === 'ADMIN') {
  state.items = itemsAdmin
} else {
  state.items = []
}

const mutations = make.mutations(state)

const actions = {
  ...make.actions(state),
  init: async ({ dispatch }) => {
    //
  },
}

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
